body {
  margin: 0;
  padding: 0;
  background-color: #F7F7F7;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.react-codemirror2 {
    font-size: 24px;
    flex: 1 1;
}

.react-codemirror2 .CodeMirror {
    height: 100%;
}

.editor-highlighted-background {
    background-color: #44535a;
}

@media (max-width: 700px) {
    .react-codemirror2 {
        font-size: 13px;
    }
}

strong {
    font-weight: 700 !important;
}

.highlighted-code {
    background-color: #44535a;
}

.sequential-highlighted-code {
    background-color: #44535a;
}
:root {
    --main: #2A363B;
    --dark-main: #1F292E;
    --one: #A7226E;
    --two: #EC2049;
    --three: #F26B38;
    --four: #F7DB4F;
    --five: #2F9599;
    --gray: #A8A7A7;
    --clear-text-color: #FFF;
    --dark-text-color: #000
}
.rst__rowContents {
    color: var(--clear-text-color);
    background-color: var(--main);
    border: none;
    border-radius: 10px;
    min-width: 0px;
    padding: 0 5px 0 25px;
}

.rst__rowTitle {
    font-weight: normal;
}

.Program .rst__rowContents {
    background-color: var(--dark-main);
}

.VariableDeclarator .rst__rowContents {
    background-color: var(--five);
}

.arguments .rst__rowContents {
    background-color: var(--three);
}

.BinaryExpression .rst__rowContents {
    background-color: var(--four);
    color: var(--dark-text-color);
}

.UnaryExpression .rst__rowContents {
    background-color: var(--four);
    color: var(--dark-text-color);
}

.AssignmentExpression .rst__rowContents {
    background-color: var(--four);
    color: var(--dark-text-color);
}

.Identifier .rst__rowContents {
    background-color: var(--one);
}

.Literal .rst__rowContents {
    background-color: var(--two);
}

.tabs {
    margin: 10px 10px 10px 0;
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 3px;
    height: calc(100% - 25px);
}

.tabs__labels {
    margin: 0;
    padding: 0;
}

.tabs__labels li {
    display: inline-block;
}

.tabs__labels li a {
    padding: 8px 50px;
    display: block;
    color: #444;
    text-decoration: none;
    border-bottom: 3px solid #f5f5f5;
}

.tabs__labels li a.active {
    border-bottom-color: var(--five);
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.tabs__content {
    padding: 25px;
    height: calc(100% - 40px);
}

.tabs__content > div:first-child {
    height: inherit;
}
